}



.AddButton{
    height: 50px;
    width: 200px;
    font-size: 20px;
}

.BtnContainer{
    display: flex;
    justify-content: space-around;
}
.Modal{
    z-index: 500;
    border: 1px solid black;
    height: 50%;
    width: 500px;
    position: absolute;
    left: 30%;
    top: 20%;
    -webkit-animation: slide 0.5s 1 ease-in-out;
            animation: slide 0.5s 1 ease-in-out;
}

@-webkit-keyframes slide{
    
    0%{
        top: -80%;
    }

    100%{
        top: 20%;
    }

}

@keyframes slide{
    
    0%{
        top: -80%;
    }

    100%{
        top: 20%;
    }

}
.Backdrop{
    position: fixed;
    z-index: 100;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
}

